/* colors */
* {
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear; }

body {
  font-family: sans-serif;
  margin: 0;
  padding: 0; }

.App {
  text-align: center; }

.controls {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; }

.randomize {
  cursor: pointer;
  margin: 20px 10px;
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
  width: 100px; }
  .randomize.is-active {
    background: #7c8394;
    color: #fff; }
  .randomize:hover {
    background: #7c8394; }
  .randomize:focus {
    outline: 0; }



